// 'use strict'
// –æ–±—ä–µ–∫—Ç –∫–æ—Ä–∑–∏–Ω—ã
let cart = {
    items: {},
    // –¥–æ–±–∞–≤–∏–ª –æ–±—ä–µ–∫—Ç, –≤ –∫–æ—Ä–∑–∏–Ω–µ, —Å–æ–¥–µ—Ä–∂–∞—â–∏–π –≤—Å–µ —Ç–æ–≤–∞—Ä—ã, –∏–Ω–∞—á–µ –±—ã–ª–∏ –ø—Ä–æ–±–ª–µ–º—ã —Å –ø–µ—Ä–µ—á–∏—Å–ª–µ–Ω–∏–µ–º
    addCart(id, price, input) {
        // —Å–æ–∑–¥–∞–Ω–∏–µ –Ω–æ–≤–æ–≥–æ —Ç–æ–≤–∞—Ä–∞, –µ—Å–ª–∏ –µ–≥–æ –Ω–µ—Ç –≤ –∫–æ—Ä–∑–∏–Ω–µ
        if (!this['items'].hasOwnProperty(id)) {
            this['items'][id] = {};
            this['items'][id]['price'] = +price;
            this['items'][id]['count'] = 1;
            this['items'][id]['totalPrice'] = this['items'][id]['count'] * this['items'][id]['price'];
            console.log(this);
        } else {
            this['items'][id]['count'] += 1;
            this['items'][id]['totalPrice'] = this['items'][id]['count'] * this['items'][id]['price'];
            console.log(this);
        }
        this.updInputVal(input, this['items'][id]['count']);
    },
    delCart(id, input) {
        if (this['items'].hasOwnProperty(id)) {
            if (this['items'][id]['count'] - 1 <= 0) {
                this.updInputVal(input, 0);
                delete this['items'][id]
                console.log(this);
                return;
            }
            this['items'][id]['count'] -= 1;
            this['items'][id]['totalPrice'] = this['items'][id]['count'] * this['items'][id]['price'];
            this.updInputVal(input, this['items'][id]['count']);
            console.log(this);
        } else {
            return;
        }
    },
    delAllCart(inputs) {
        for (let item in this.items) {
            delete this.items[item];
        }
        inputs.forEach(input => {
            this.updInputVal(input, 0);
        });
    },

    updInputVal(tag, count) {
        tag.value = count;
    }
};
// –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏–µ –¥–ª—è —É–¥–æ–±—Å—Ç–≤–∞
// openCart  - –ø–æ–∫–∞–∑–∞—Ç—å —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ –∫–æ—Ä–∑–∏–Ω—ã

let d = document;
// —ç–ª–µ–º–µ–Ω—Ç, —Å–æ–¥–µ—Ä–∂–∞—â–∏–π –≤—Å–µ –ø—Ä–æ–¥—É–∫—Ç—ã
let products = d.querySelector('.products');
let clearBtn = d.querySelector('.clear-btn');
let btnFirstAdd = d.querySelectorAll('.product__btn-add');
// –í–µ—à–∞–µ–º —Å–æ–±—ã—Ç–∏—è –Ω–∞ –≤—Å–µ –∫–Ω–æ–ø–∫–∏

